import common from '../../common/common.js'
export default {
	data() {
		return {
			info:{},
			picUrl:''
		}
	},
	onLoad(options) {
		var id = options.id;
		this.getInfo(id);
		this.picUrl=common.picUrl
	},
	methods: {
		getInfo(id) {
			var data = {
				page: 1,
				pageSize: 5,
				recordId:id,
			}
			var that = this;
			common.getData('/muqiang/invitation/getWarnList', data, (res) => {
				console.log(res);
				this.info= res.pageInfo.list[0];
			})
		}
	}
}
